import React, { PureComponent } from "react";
import { Table, Divider } from "antd";

type PersonnelData = {
  key?: number;
  name: string;
  position: string;
  phone: string;
  basicWage: string;
  status: string;
  entryTime: string;
  jobNumber: string;
};

interface Props {
  dataSource: PersonnelData[];
  onOpen: (record: any) => void;
  onDelete: (record: any) => void;
}

export default class PersonnelTable extends PureComponent<Props> {
  render() {
    const { dataSource } = this.props;
    const data = dataSource.map((item, index) => {
      return { ...item, key: index}
    })
    return <Table columns={this.renderColumns()} dataSource={data} />;
  }

  private renderColumns = () => {
    return [
      {
        title: "",
        dataIndex: "name",
        key: "name"
      },
      {
        title: "",
        dataIndex: "position",
        key: "position"
      },
      {
        title: "",
        dataIndex: "phone",
        key: "phone"
      },
      {
        title: "",
        key: "basicWage",
        dataIndex: "basicWage"
      },
      {
        title: "",
        key: "status",
        dataIndex: "status"
      },
      {
        title: "",
        key: "entryTime",
        dataIndex: "entryTime"
      },
      {
        title: "",
        key: "jobNumber",
        dataIndex: "jobNumber"
      },
      {
        title: "",
        key: "action",
        render: (text, record) => (
          <span>
            <a href="javascript:;" onClick={() => this.props.onOpen(record)}></a>
            <Divider type="vertical" />
            <a href="javascript:;" onClick={() => this.props.onDelete(record)}></a>
          </span>
        )
      }
    ];
  }
}



