import React, { PureComponent } from "react";
import { Table } from "antd";

type PersonnelData = {
  key?: number;
  personnelName: string;
  position: string;
  commissionType: string;
  transSerno: string;
  name: string;
  createDate: string;
  cnAmt: string;
};

interface Props {
  dataSource: PersonnelData[];
  onDelete: () => void;
}

export default class CommissionTable extends PureComponent<Props> {
  render() {
    const { dataSource } = this.props;
    const data = dataSource.map((item, index) => {
      return { ...item, key: index}
    })
    return <Table columns={this.renderColumns()} dataSource={data} />;
  }

  private renderColumns = () => {
    return [
      {
        title: "",
        dataIndex: "personnelName",
        key: "personnelName"
      },
      {
        title: "",
        dataIndex: "position",
        key: "position"
      },
      {
        title: "",
        dataIndex: "commissionType",
        key: "commissionType"
      },
      {
        title: "",
        key: "transSerno",
        dataIndex: "transSerno"
      },
      {
        title: "",
        key: "name",
        dataIndex: "name"
      },
      {
        title: "",
        key: "createDate",
        dataIndex: "createDate"
      },
      {
        title: "",
        key: "cnAmt",
        dataIndex: "cnAmt"
      },
      {
        title: "Action",
        key: "action",
        render: (text, record) => (
          <span>
            <a href="javascript:;" onClick={this.props.onDelete}></a>
          </span>
        )
      }
    ];
  }
}



