import React, { PureComponent } from "react";
import { Form, Input } from "antd";
import { WrappedFormUtils } from "antd/lib/form/Form";

const FormItem = Form.Item;

interface Props {
  form: WrappedFormUtils;
}

class ModifyForm extends PureComponent<Props> {
  render() {
    const formItemLayout = {
      labelCol: {
        xs: { span: 28 },
        sm: { span: 4 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 20 }
      }
    };
    const {
      form: { getFieldDecorator }
    } = this.props;
    return (
      <Form>
        <FormItem {...formItemLayout} label="">
          {getFieldDecorator("name", {
            rules: [{ required: true, message: "" }]
          })(<Input name="name" type="text" placeholder="" />)}
        </FormItem>
        <FormItem {...formItemLayout} label="">
          {getFieldDecorator("position", {
            rules: [{ required: true, message: "" }]
          })(
            <Input name="position" type="text" placeholder="" />
          )}
        </FormItem>
        <Form.Item {...formItemLayout} label="">
          {getFieldDecorator("phone", {
            rules: [{ required: true, message: "" }]
          })(
            <Input name="phone" type="tel" placeholder="" />
          )}
        </Form.Item>
        <FormItem {...formItemLayout} label="">
          {getFieldDecorator("basicWage", {
            rules: [{ required: true, message: "" }]
          })(
            <Input
              name="basicWage"
              type="text"
              placeholder=""
            />
          )}
        </FormItem>
        <FormItem {...formItemLayout} label="">
          {getFieldDecorator("status", {
            rules: [{ required: true, message: "" }]
          })(<Input name="status" type="text" placeholder="" />)}
        </FormItem>
      </Form>
    );
  }
}

export default Form.create({})(ModifyForm);



